import Button from '@material-ui/core/Button';
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import React, { useState } from 'react';

import styles from './Poem.css';

const Poem = ({ body, className, title }) => {
  const classes = makeStyles(styles)();
  const [expanded, setExpanded] = useState(false);

  return (
    <Paper key={title} className={classNames(classes.poem, className)}>
      <Typography className={classes.poemTitle} variant="h6">
        {title}
      </Typography>
      <Collapse
        className={classes.collapse}
        collapsedHeight="200px"
        in={expanded}>
        <div>
          <Typography className={classes.poemBody} variant="body2">
            {body.trim()}
          </Typography>
          {!expanded && <div className={classes.fade} />}
        </div>
      </Collapse>
      {!expanded && (
        <Button
          className={classes.readMore}
          color="secondary"
          onClick={() => {
            setExpanded(true);
          }}>
          Read More
        </Button>
      )}
    </Paper>
  );
};

ResearchPaper.defaultProps = {
  className: null,
};

Poem.propTypes = {
  body: PropTypes.string.isRequired,
  className: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default Poem;
